import React from 'react'
import ReactMarkdown from 'react-markdown'
import { Link } from 'react-router-dom'
import noteStore, { INote } from '../NoteStore'

const NoteItem = ({ note }: { note: INote }) => {
	const setActiveNote = noteStore(state => state.setActiveNote)

	const openNote = () => {
		setActiveNote(note._id)
	}

	const renderNoteText = () => {
		if (note.text.length > 0) {
			return note.text
		} else {
			return 'Нет текста'
		}
	}

	return (
		<Link
			to={'/Note'}
			className='decoration-transparent mb-0'
			onClick={openNote}
		>
			<h4 className='mb-3 mt-0 break-words text-slate-800'>{note.title}</h4>
		</Link>
	)
}

export default NoteItem
