import React from 'react'
import ReactMarkdown from 'react-markdown'
import remarkGfm from 'remark-gfm'
import noteStore, { INote } from '../NoteStore'
import { ImCancelCircle } from 'react-icons/im'
import { Link, useNavigate } from 'react-router-dom'

const Note = ({ thisNote }: { thisNote: INote }) => {
	const { setNoteText, notes, setNoteTitle, removeNote } = noteStore(
		state => state
	)

	const navigate = useNavigate()

	let noteText = ''

	notes.forEach(note => {
		if (note._id === thisNote._id) {
			noteText = note.text
		}
	})

	const setText = (newText: React.ChangeEvent<HTMLTextAreaElement>) => {
		// setNoteText(newText.target.value)
		setNoteText(thisNote._id, newText.target.value)
	}

	const setTitle = (newTitle: React.ChangeEvent<HTMLInputElement>) => {
		if (newTitle.target.value.length <= 0) {
			navigate('/')
			removeNote(thisNote._id)
			return
		}

		setNoteTitle(thisNote._id, newTitle.target.value)
	}

	return (
		<div>
			<div className='fixed pl-3 pt-3 pb-2 shadow-md w-screen bg-white flex flex-row'>
				<Link to='/'>
					<ImCancelCircle color='orange' size={30} />
				</Link>
				<input
					defaultValue={thisNote.title}
					onChange={setTitle}
					maxLength={40}
					className='text-slate-800 outline-none border-0 w-112 font-bold font-montserrat m-auto pr-14 text-center text-lg'
				/>
			</div>

			<div className='flex md:flex-row flex-col'>
				<textarea
					defaultValue={noteText}
					className='h-screen resize-none outline-none border-0 md:border-r-2 border-b-2 pt-16 pr-2 pl-3 md:w-1/2 w-screen text-base font-montserrat'
					onChange={setText}
				/>
				<ReactMarkdown
					remarkPlugins={[remarkGfm]}
					className=' pl-2 h-screen break-words text-base mt-14 md:w-1/2 w-screen'
				>
					{noteText}
				</ReactMarkdown>
			</div>
		</div>
	)
}

export default Note
