import React from 'react'
import noteStore from '../NoteStore'
import Note from './Note'

const NotePage = () => {

    const {activeNote, notes} = noteStore(state => state)

  return (
    <div>
      
        {notes.map(note => {if(note._id === activeNote){
            return(<Note thisNote={note} key={note._id}/>)
        }})}
    </div>
  )
}

export default NotePage