import create from 'zustand'
import { persist } from 'zustand/middleware'

export interface INote {
	title: string
	text: string
	_id: number
}

interface NotesStore {
	notes: INote[]
	addNote: (newNote: INote) => void
	removeNote: (noteId: number) => void
	activeNote: number
	setActiveNote: (newActiveNote: number) => void
	setNoteText: (noteId: number, newText: string) => void
	setNoteTitle: (noteId: number, newTitle: string) => void
}

const noteStore = create(
	persist<NotesStore>(set => ({
		notes: [],
		addNote: newNote => set(state => ({ notes: [...state.notes, newNote] })),
		removeNote: noteId =>
			set(state => ({
				notes: state.notes.filter(note => note._id !== noteId),
			})),
		activeNote: 0,
		setActiveNote: newActiveNote => set(() => ({ activeNote: newActiveNote })),
		setNoteText: (noteId, newText) =>
			set(state => ({
				notes: state.notes.map(note => {
					if (note._id === noteId) {
						return { _id: note._id, text: newText, title: note.title }
					} else {
						return note
					}
				}),
			})),
		setNoteTitle: (noteId, newTitle) =>
			set(state => ({
				notes: state.notes.map(note => {
					if (note._id === noteId) {
						return { _id: note._id, text: note.text, title: newTitle }
					} else {
						return note
					}
				}),
			})),
	}))
)

export default noteStore
