import React, { useRef } from 'react'
import NoteItem from './NoteItem'
import noteStore, { INote } from '../NoteStore'
import useInput from '../useInput'

const AllNotes = () => {
	const notes = noteStore(state => state.notes)
	const addNoteReducer = noteStore(state => state.addNote)
	const { value, bind, reset } = useInput('')

	const ref = useRef<HTMLInputElement>(null)

	const addNote = () => {
		const newNote: INote = {
			title: value,
			text: '',
			_id: Math.random(),
		}

		if (ref.current !== null) {
			ref.current.focus()
			reset()
		}

		addNoteReducer(newNote)
	}

	return (
		<div className='pt-5 lg:mx-24 mx-4'>
			<div>
				<div className=' flex flex-col'>
					{notes.map(note => (
						<NoteItem key={note._id} note={note} />
					))}
				</div>

				<input
					maxLength={40}
					placeholder={'Название новой заметки'}
					ref={ref}
					className=' outline-none mt-3 border-0 border-b-2 text-base font-montserrat mr-4'
					{...bind}
				/>
				{value.length > 0 && (
					<button
						onClick={addNote}
						className='font-montserrat border-0 bg-transparent text-base text-slate-600'
					>
						Создать заметку
					</button>
				)}
			</div>
		</div>
	)
}

export default AllNotes
